"use client";
import { useEffect, useState } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { CheckCircle, XCircle, User, Calendar, ClipboardList } from 'lucide-react';
import { toast } from 'sonner';
import { getAppointments, updateAppointmentStatus } from '@/app/actions';

export default function CounselorDashboard() {
  const { currentUser } = useAppStore();
  const [appointments, setAppointments] = useState<any[]>([]);

  const loadData = () => {
    if (currentUser?.id) {
      getAppointments(currentUser.id, 'counselor').then(setAppointments);
    }
  };

  useEffect(() => { loadData() }, [currentUser]);

  const handleProcess = async (id: string, status: string) => {
    await updateAppointmentStatus(id, status);
    toast.success(status === 'confirmed' ? "已批准预约" : "已拒绝预约");
    loadData();
  };

  // 分类数据
  const pendingApps = appointments.filter(a => a.status === 'pending');
  const confirmedApps = appointments.filter(a => a.status === 'confirmed');

  return (
    <div className="space-y-8">
      <div className="flex justify-between items-center">
         <h1 className="text-2xl font-bold text-slate-800">咨询师工作台</h1>
      </div>
      
      {/* --- 待处理区域 --- */}
      <Card className="border-l-4 border-l-blue-500 shadow-sm">
        <CardHeader><CardTitle className="text-blue-700 flex items-center gap-2">待审批预约 ({pendingApps.length})</CardTitle></CardHeader>
        <CardContent>
          <div className="space-y-4">
            {pendingApps.length === 0 ? <p className="text-slate-400 text-sm">暂无待处理事项</p> : 
              pendingApps.map((apt) => (
                <div key={apt.id} className="flex flex-col md:flex-row justify-between items-start md:items-center p-4 border rounded-lg bg-slate-50 gap-4">
                  <div className="flex gap-4 items-center">
                    <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center border shadow-sm font-bold text-blue-600">
                      {apt.student.name[0]}
                    </div>
                    <div>
                      <div className="font-bold text-slate-800 flex items-center gap-2">
                          {apt.student.name}
                          <Badge variant="outline" className="bg-white">{apt.problemType}</Badge>
                      </div>
                      <div className="text-sm text-slate-500 mt-1">
                        申请时间: {new Date(apt.date).toLocaleDateString()} {apt.time}
                      </div>
                      
                      {/* 查看档案详情 */}
                      <Dialog>
                        <DialogTrigger asChild>
                            <Button variant="link" className="p-0 h-auto text-blue-600 text-xs mt-1">查看详细档案 &gt;</Button>
                        </DialogTrigger>
                        <DialogContent className="max-w-lg">
                            <DialogHeader><DialogTitle>学生档案: {apt.student.name}</DialogTitle></DialogHeader>
                            <StudentDetailView apt={apt} />
                        </DialogContent>
                      </Dialog>
                    </div>
                  </div>
                  
                  <div className="flex gap-2 w-full md:w-auto">
                    <Button size="sm" variant="outline" className="text-red-600 hover:bg-red-50 flex-1 md:flex-none" onClick={() => handleProcess(apt.id, 'rejected')}>
                      <XCircle className="w-4 h-4 mr-1" /> 拒绝
                    </Button>
                    <Button size="sm" className="bg-[#5D9C59] hover:bg-[#4a8546] flex-1 md:flex-none" onClick={() => handleProcess(apt.id, 'confirmed')}>
                      <CheckCircle className="w-4 h-4 mr-1" /> 批准
                    </Button>
                  </div>
                </div>
              ))
            }
          </div>
        </CardContent>
      </Card>

      {/* --- 即将进行的咨询 (含测评结果) --- */}
      <Card>
        <CardHeader><CardTitle>即将进行的咨询 ({confirmedApps.length})</CardTitle></CardHeader>
        <CardContent>
            <div className="space-y-4">
                {confirmedApps.map((apt) => (
                    <div key={apt.id} className="p-4 border rounded-lg flex flex-col md:flex-row justify-between gap-4">
                        <div>
                             <div className="font-bold text-lg flex items-center gap-2">
                                {apt.student.name}
                                <span className="text-sm font-normal text-slate-500">({apt.studentIdNum})</span>
                             </div>
                             <div className="text-sm text-slate-500 mt-1 flex items-center gap-2">
                                <Calendar className="w-3 h-3"/> {new Date(apt.date).toLocaleDateString()} {apt.time}
                             </div>
                             {/* 查看档案详情 */}
                             <Dialog>
                                <DialogTrigger asChild>
                                    <Button variant="link" className="p-0 h-auto text-slate-400 text-xs mt-1">回顾档案 &gt;</Button>
                                </DialogTrigger>
                                <DialogContent className="max-w-lg">
                                    <DialogHeader><DialogTitle>学生档案: {apt.student.name}</DialogTitle></DialogHeader>
                                    <StudentDetailView apt={apt} />
                                </DialogContent>
                             </Dialog>
                        </div>

                        {/* 测评结果展示区 */}
                        <div className="flex items-center">
                            {apt.testResult ? (
                                <div className="bg-amber-50 border border-amber-100 rounded-lg p-3 min-w-[200px]">
                                    <div className="text-xs font-bold text-amber-700 mb-2 flex items-center gap-1">
                                        <ClipboardList className="w-3 h-3"/> 心理测评结果 (仅咨询师可见)
                                    </div>
                                    <div className="grid grid-cols-3 gap-2 text-center text-sm">
                                        <div className="bg-white rounded p-1">
                                            <div className="text-xs text-slate-400">SDS</div>
                                            <div className={`font-bold ${apt.testResult.sdsScore > 10 ? 'text-red-500' : 'text-slate-700'}`}>
                                                {apt.testResult.sdsScore}
                                            </div>
                                        </div>
                                        <div className="bg-white rounded p-1">
                                            <div className="text-xs text-slate-400">SAS</div>
                                            <div className={`font-bold ${apt.testResult.sasScore > 10 ? 'text-red-500' : 'text-slate-700'}`}>
                                                {apt.testResult.sasScore}
                                            </div>
                                        </div>
                                        <div className="bg-white rounded p-1">
                                            <div className="text-xs text-slate-400">SES</div>
                                            <div className="font-bold text-slate-700">{apt.testResult.sesScore}</div>
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <div className="text-sm text-slate-400 bg-slate-50 px-4 py-2 rounded-lg italic">
                                    学生尚未完成前测
                                </div>
                            )}
                        </div>
                    </div>
                ))}
            </div>
        </CardContent>
      </Card>
    </div>
  );
}

// 抽离出来的档案详情组件 (仅内部使用)
function StudentDetailView({ apt }: { apt: any }) {
    return (
        <div className="grid grid-cols-2 gap-y-4 gap-x-6 text-sm py-2">
            <div><span className="text-slate-500 block text-xs">手机号</span> {apt.phone}</div>
            <div><span className="text-slate-500 block text-xs">学院</span> {apt.college}</div>
            <div><span className="text-slate-500 block text-xs">专业</span> {apt.major}</div>
            <div><span className="text-slate-500 block text-xs">班级</span> {apt.classInfo}</div>
            
            <div className="col-span-2 border-t pt-2 mt-2">
                <span className="font-bold text-slate-700 block mb-1">咨询问题类型</span> 
                <Badge variant="secondary">{apt.problemType}</Badge>
            </div>
            
            <div className="col-span-2">
                <span className="font-bold text-slate-700 block mb-1">精神疾病史</span> 
                <p className="bg-slate-50 p-2 rounded text-slate-600">{apt.mentalHistory || '无'}</p>
            </div>
            
            <div className="col-span-2">
                <span className="font-bold text-slate-700 block mb-1">目前身心状态</span> 
                <p className="bg-slate-50 p-2 rounded text-slate-600">{apt.physicalState || '无'}</p>
            </div>
        </div>
    );
}